px 95px;\r\n}\r\n\r\n/*------------------------------------------------------------------\r\n[  ]*/\r\n\r\n.login100-pic {\r\n  width: 316px;\r\n}\r\n\r\n.login100-pic img {\r\n  max-width: 100%;\r\n}\r\n\r\n/*------------------------------------------------------------------\r\n[  ]*/\r\n\r\n.login100-form {\r\n  width: 290px;\r\n}\r\n\r\n.login100-form-title {\r\n  font-family: Poppins-Bold;\r\n  font-size: 24px;\r\n  color: #333333;\r\n  line-height: 1.2;\r\n  text-align: center;\r\n\r\n  width: 100%;\r\n  display: block;\r\n  padding-bottom: 54px;\r\n}\r\n\r\n/*---------------------------------------------*/\r\n\r\n.wrap-input100 {\r\n  position: relative;\r\n  width: 100%;\r\n  z-index: 1;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.input100 {\r\n  font-family: Poppins-Medium;\r\n  font-size: 15px;\r\n  line-height: 1.5;\r\n  color: #666666;\r\n\r\n  display: block;\r\n  width: 100%;\r\n  background: #e6e6e6;\r\n  height: 50px;\r\n  border-radius: 25px;\r\n  padding: 0 30px 0 68px;\r\n}\r\n\r\n/*------------------------------------------------------------------\r\n[ Focus ]*/\r\n\r\n.focus-input100 {\r\n  display: block;\r\n  position: absolute;\r\n  border-radius: 25px;\r\n  bottom: 0;\r\n  left: 0;\r\n  z-index: -1;\r\n  width: 100%;\r\n  height: 100%;\r\n  box-shadow: 0px 0px 0px 0px;\r\n  color: rgba(87,184,70, 0.8);\r\n}\r\n\r\n.input100:focus + .focus-input100 {\r\n  -webkit-animation: anim-shadow 0.5s ease-in-out forwards;\r\n  animation: anim-shadow 0.5s ease-in-out forwards;\r\n}\r\n\r\n@-webkit-keyframes anim-shadow {\r\n  to {\r\n    box-shadow: 0px 0px 70px 25px;\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n@keyframes anim-shadow {\r\n  to {\r\n    box-shadow: 0px 0px 70px 25px;\r\n    opacity: 0;\r\n  }\r\n}\r\n\r\n.symbol-input100 {\r\n  font-size: 15px;\r\n  display: flex;\r\n  align-items: center;\r\n  position: absolute;\r\n  border-radius: 25px;\r\n  bottom: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  padding-left: 35px;\r\n  pointer-events: none;\r\n  color: #666666;\r\n  transition: all 0.4s;\r\n}\r\n\r\n.input100:focus + .focus-input100 + .symbol-input100 {\r\n  color: #57b846;\r\n  padding-left: 28px;\r\n}\r\n\r\n/*------------------------------------------------------------------\r\n[ Button ]*/\r\n\r\n.container-login100-form-btn {\r\n  width: 100%;\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n  padding-top: 20px;\r\n}\r\n\r\n.login100-form-btn {\r\n  font-family: Montserrat-Bold;\r\n  font-size: 15px;\r\n  line-height: 1.5;\r\n  color: #fff;\r\n  text-transform: uppercase;\r\n\r\n  width: 100%;\r\n  height: 50px;\r\n  border-radius: 25px;\r\n  background: #57b846;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  padding: 0 25px;\r\n  transition: all 0.4s;\r\n}\r\n\r\n.login100-form-btn:hover {\r\n  background: #333333;\r\n}\r\n\r\n/*------------------------------------------------------------------\r\n[ Responsive ]*/\r\n\r\n@media (max-width: 992px) {\r\n  .wrap-login100 {\r\n    padding: 177px 90px 33px 85px;\r\n  }\r\n\r\n  .login100-pic {\r\n    width: 35%;\r\n  }\r\n\r\n  .login100-form {\r\n    width: 50%;\r\n  }\r\n}\r\n\r\n@media (max-width: 768px) {\r\n  .wrap-login100 {\r\n    padding: 100px 80px 33px 80px;\r\n  }\r\n\r\n  .login100-pic {\r\n    display: none;\r\n  }\r\n\r\n  .login100-form {\r\n    width: 100%;\r\n  }\r\n}\r\n\r\n@media (max-width: 576px) {\r\n  .wrap-login100 {\r\n    padding: 100px 15px 33px 15px;\r\n  }\r\n}\r\n\r\n/*------------------------------------------------------------------\r\n[ Alert validate ]*/\r\n\r\n.validate-input {\r\n  position: relative;\r\n}\r\n\r\n.alert-validate::before {\r\n  content: attr(data-validate);\r\n  position: absolute;\r\n  max-width: 70%;\r\n  background-color: white;\r\n  border: 1px solid #c80000;\r\n  border-radius: 13px;\r\n  padding: 4px 25px 4px 10px;\r\n  top: 50%;\r\n  -webkit-transform: translateY(-50%);\r\n  transform: translateY(-50%);\r\n  right: 8px;\r\n  p